
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
components: {
ViewBox,
XHeader,
Flexbox,
FlexboxItem,
Group,
Cell,
GridItem,
Grid,
},
data() {
return {
title1: '新人手册',
title2: '高手进阶',
title3: '捕手风采'
}
},
methods: {

toMember() {
this.$router.push({
path: "/cash/cash"
});
},

toCoupon() {
this.$router.push({
path: "/coupon/coupon"
});
},

toRefill() {
this.$router.push({
path: "/housekeeper/housekeeper"
});
},

toCull() {
this.$router.push({
path: "/customer/customer"
});
},

toPick() {
this.$router.push({
path: "/saleOrder/saleOrder"
});
},

toMessage(title1) {
this.$router.push({
path: '/message/message',
name: '/message/message',
params: {
title: title1
}
})
},
},
mounted() {},
}



